/**
* Initialize flickity
*/

const initFlickity = () => {
  const achieve = document.querySelector('.js-achieve');

  if (!achieve) return
  if (screen.width < 768) {
    //@ts-ignore
    new Flickity(achieve, {
      // options
      cellAlign: 'left',
      autoPlay: false,
      prevNextButtons: true,
      wrapAround: true
    });
  }

}

export default initFlickity;